
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.home canvas{width:100% !important
}
.home .notice{background:rgba(0,0,0,0.2)
}
.home .notice2{background:#3ccdd7ad;position:relative
}
.home .notice2 .notice-text{position:absolute;top:50%
}
.home .notice-text{font-size:10px;border:1px solid white;border-radius:15px;color:white
}
.home .notice-text:hover,.home .notice-text:active{font-size:12px;border:2px solid #fe0;color:#fe0;border-radius:15px
}
*{font-family:PingFangTC;line-height:1.3 !important
}
.h8{font-size:2rem
}
.h7{font-size:1.25rem
}
.h6{font-size:1.125rem
}
.h5{font-size:1rem
}
.h4{font-size:0.875rem
}
.h3{font-size:0.75rem
}
.h2{font-size:0.625rem
}
.h1{font-size:0.5rem
}
